import {useEffect, useState} from 'react';
import { useNavigate } from 'react-router-dom';
import swal from 'sweetalert';
import Header from '../Header';
function AddStudent(){
    const [firstName, setFirstName] =useState('');
    const [lastName, setLastName] =useState('');
    const [fatherName, setFatherName] =useState('');
    const [date, setDate] =useState('');
    const [college, setCollege] =useState('');
    const [level, setLevel] =useState('');
    const [room, setRoom] =useState('');
    const [number, setNumber] =useState('');
    const navigate = useNavigate();
    let user = JSON.parse(localStorage.getItem('user-info'));
    useEffect(() => {
        if(!user)
        {
            navigate('/');
        }
        else
        {   
            if(user.role=='student')
            {
                navigate('/');
            }
        }

    },[])
    async function add(){
        
        if(!(firstName && lastName && fatherName && date && college && level && room && number)){
            swal("", "   ", "error");
            return
        }
        
        console.warn(firstName, lastName, fatherName,
            date, college, level, room, number);

        const formData=new FormData();
        formData.append('firstName',firstName);
        formData.append('lastName',lastName);
        formData.append('fatherName',fatherName);
        formData.append('birthDay',date);
        formData.append('college',college);
        formData.append('level',level);
        formData.append('room',room);
        formData.append('phoneNumber',number);

        let result = await fetch("http://127.0.0.1:8000/api/Students/add", {
            method:"POST",
            body:formData
        });
        swal("", "   ", "success");
    }

    return(

        <>
        <h1 className="text-success"> </h1>
            <table cellPadding='0' cellSpacing='0' style={{textlign: "right"}} className='table table-hover table-striped'>
            <tbody> 
            <tr><td><input type="text"  autoFocus
            onChange={(e)=>setFirstName(e.target.value)} /> </td><td></td> </tr>

            <tr><td>  <input type="text" 
            onChange={(e)=>setLastName(e.target.value)} /> </td><td></td> </tr>

            <tr><td>  <input type="text" 
            onChange={(e)=>setFatherName(e.target.value)} /> </td><td>  </td> </tr>

            <tr><td>  <input type="date" 
            onChange={(e)=>setDate(e.target.value)} /> </td><td>  </td> </tr>

            <tr><td>   <select onChange={(e)=>setCollege(e.target.value)}>
            <option  value="">-- --</option>
            <option value="   ">   </option>
            <option value="  ">  </option>
            <option value="  ">   </option>
            <option value="  ">   </option>
            <option value ="    ">  </option>
            <option value ="  ">  </option> 
            <option value ="  ">  </option>
            </select></td><td> </td> </tr> 

            <tr><td><select onChange={(e)=>setLevel(e.target.value)}>
            <option  value="">-- --</option>
            <option  value="1"></option>
            <option  value="2"></option>
            <option  value="3"></option>
            <option  value="4"></option>
            </select></td><td>   </td> </tr>

            <tr><td><input type="number"
            onChange={(e)=>setRoom(e.target.value)}/> </td><td> </td> </tr>

            <tr><td><input type="text"
            onChange={(e)=>setNumber(e.target.value)} /> </td><td>  </td> </tr>
            </tbody>
            </table>
            <button onClick={add} className='btn btn-success'> submit </button>
        </>
    )
}

export default AddStudent;